import { Component, OnInit } from '@angular/core';
import { PasService } from '../../services/pas.service';

import { Pas } from '../../models/Pas';

@Component({
  selector: 'app-psi',
  templateUrl: './psi.component.html',
  styleUrls: ['./psi.component.css']
})
export class PsiComponent implements OnInit {
  psi:Pas[];

  constructor(private pasService:PasService) { }

  ngOnInit() {
    this.pasService.getPsi().subscribe(psi => {
      this.psi = psi;
    });
  }

  deletePas(pas:Pas) {
    this.psi = this.psi.filter(p => p.id !== pas.id);
    this.pasService.deletePas(pas).subscribe();
  }

  addPas(pas:Pas) {
    this.pasService.addPas(pas).subscribe(pas => {
      this.psi.push(pas);
    });
  }

}
