import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Pas } from '../models/Pas';

const httpOptions = {
    headers: new HttpHeaders({
        'Content-Type': 'application/json'
    })
}

@Injectable({
    providedIn: 'root'
})
export class PasService {
    psiUrl: string = 'http://localhost:3000/psi';

    psiLimit = '?_limit=10';    

    constructor(private http: HttpClient) { }

    getPsi(): Observable<Pas[]> {
        return this.http.get<Pas[]>(`${this.psiUrl}${this.psiLimit}`);
    }

    deletePas(pas: Pas): Observable<Pas> {
        const url = `${this.psiUrl}/${pas.id}`;
        return this.http.delete<Pas>(url, httpOptions);
    }

    addPas(pas: Pas): Observable<Pas> {
        return this.http.post<Pas>(this.psiUrl, pas, httpOptions);
    }

    toggleCompleted(pas: Pas): Observable<any> {
        const url = `${this.psiUrl}/${pas.id}`;
        return this.http.put(url, pas, httpOptions);
    }
}